'use client'

import { useState, useEffect, useRef, useCallback } from 'react'
import { usePathname } from 'next/navigation'
import { isAdminPage } from '@/lib/adminConfig'
import { Moon, Sun, ChevronUp } from 'lucide-react'
import { useTheme } from '@/contexts/ThemeContext'

export default function FloatingButtons() {
  const pathname = usePathname()

  // åœ¨ enterpriseã€promo å’Œ social-feed é¡µé¢ä¸æ¸²æŸ“å…¨å±€æ‚¬æµ®æŒ‰é’®
  if (pathname.startsWith('/enterprise') || pathname.startsWith('/promo') || pathname.startsWith('/social-feed')) {
    return null
  }

  const isAdmin = isAdminPage(pathname)
  const { theme, setTheme } = useTheme()
  const [mounted, setMounted] = useState(false)
  const [showScrollTop, setShowScrollTop] = useState(false)
  const [isMobile, setIsMobile] = useState(false)
  const [isIdle, setIsIdle] = useState(true) // é™æ­¢çŠ¶æ€ - é»˜è®¤åŠé€æ˜éšè—

  // æ‹–åŠ¨ç›¸å…³çŠ¶æ€
  const [position, setPosition] = useState({ x: 0, y: 0 })
  const [isDragging, setIsDragging] = useState(false)
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 })
  const containerRef = useRef<HTMLDivElement>(null)
  const hasMoved = useRef(false)
  const idleTimer = useRef<NodeJS.Timeout | null>(null)
  const justWokeUp = useRef(false) // åˆšåˆšå”¤é†’æ ‡è®°

  // é‡ç½®é™æ­¢è®¡æ—¶å™¨ï¼ˆä»…æ‰‹æœºç«¯ç”Ÿæ•ˆï¼‰
  const resetIdleTimer = useCallback((fromWakeUp = false) => {
    if (!isMobile) return // PCç«¯ä¸å‚ä¸idleçŠ¶æ€

    if (fromWakeUp) {
      justWokeUp.current = true
      // 500msåæ‰å…è®¸ç‚¹å‡»ï¼ˆç­‰åŠ¨ç”»å®Œæˆï¼‰
      setTimeout(() => {
        justWokeUp.current = false
      }, 500)
    }
    setIsIdle(false)
    if (idleTimer.current) {
      clearTimeout(idleTimer.current)
    }
    idleTimer.current = setTimeout(() => {
      setIsIdle(true)
    }, 1000) // 1ç§’åè¿›å…¥é™æ­¢çŠ¶æ€
  }, [isMobile])

  useEffect(() => {
    // æ£€æµ‹æ˜¯å¦ä¸ºæ‰‹æœºç«¯
    const checkMobile = () => {
      const mobile = window.innerWidth < 768
      setIsMobile(mobile)
      // PCç«¯ä¸å‚ä¸idleçŠ¶æ€ï¼Œç›´æ¥è®¾ä¸ºfalseï¼ˆå®Œå…¨æ˜¾ç¤ºï¼‰
      if (!mobile) {
        setIsIdle(false)
      }
    }

    // å»¶è¿Ÿåˆå§‹åŒ–ï¼Œä¸é˜»å¡é¦–å±æ¸²æŸ“
    const initFloatingButtons = () => {
      setMounted(true)
      checkMobile()
      window.addEventListener('resize', checkMobile)

      // ä»localStorageæ¢å¤ä½ç½®
      const savedPosition = localStorage.getItem('floatingButtonsPosition')
      if (savedPosition) {
        try {
          setPosition(JSON.parse(savedPosition))
        } catch (e) {
          // å¿½ç•¥è§£æé”™è¯¯
        }
      }
    }

    // ä½¿ç”¨ requestIdleCallback å»¶è¿Ÿåˆå§‹åŒ–ï¼Œä¼˜å…ˆä¿è¯é¦–å±æ¸²æŸ“
    if ('requestIdleCallback' in window) {
      (window as any).requestIdleCallback(initFloatingButtons, { timeout: 2000 })
    } else {
      setTimeout(initFloatingButtons, 500)
    }

    return () => {
      window.removeEventListener('resize', checkMobile)
      if (idleTimer.current) {
        clearTimeout(idleTimer.current)
      }
    }
  }, [resetIdleTimer])

  useEffect(() => {
    const checkScroll = () => {
      setShowScrollTop(window.scrollY > 300)
    }

    // ç›‘å¬ docs é¡µé¢çš„è‡ªå®šä¹‰æ»šåŠ¨äº‹ä»¶
    const handleDocsScroll = (e: Event) => {
      const customEvent = e as CustomEvent<{ scrollTop: number; canScrollTop: boolean }>
      setShowScrollTop(customEvent.detail.canScrollTop)
    }

    window.addEventListener('scroll', checkScroll)
    window.addEventListener('docsPageScroll', handleDocsScroll)

    return () => {
      window.removeEventListener('scroll', checkScroll)
      window.removeEventListener('docsPageScroll', handleDocsScroll)
    }
  }, [])

  // ä¿å­˜ä½ç½®åˆ°localStorage
  const savePosition = useCallback((pos: { x: number; y: number }) => {
    localStorage.setItem('floatingButtonsPosition', JSON.stringify(pos))
  }, [])

  // è§¦æ‘¸å¼€å§‹
  const handleTouchStart = useCallback((e: React.TouchEvent) => {
    if (!isMobile) return
    const touch = e.touches[0]
    setDragStart({ x: touch.clientX - position.x, y: touch.clientY - position.y })
    setIsDragging(true)
    hasMoved.current = false
    resetIdleTimer()
  }, [isMobile, position, resetIdleTimer])

  // è§¦æ‘¸ç§»åŠ¨
  const handleTouchMove = useCallback((e: React.TouchEvent) => {
    if (!isDragging || !isMobile) return
    e.preventDefault()
    const touch = e.touches[0]
    const newX = touch.clientX - dragStart.x
    const newY = touch.clientY - dragStart.y

    // è¾¹ç•Œé™åˆ¶
    const container = containerRef.current
    if (container) {
      const rect = container.getBoundingClientRect()
      const maxX = window.innerWidth - rect.width
      const maxY = window.innerHeight - rect.height

      const clampedX = Math.max(-maxX, Math.min(0, newX))
      const clampedY = Math.max(-maxY, Math.min(0, newY))

      setPosition({ x: clampedX, y: clampedY })
      hasMoved.current = true
    }
  }, [isDragging, isMobile, dragStart])

  // è§¦æ‘¸ç»“æŸ
  const handleTouchEnd = useCallback(() => {
    if (isDragging && hasMoved.current) {
      savePosition(position)
    }
    setIsDragging(false)
    resetIdleTimer()
  }, [isDragging, position, savePosition, resetIdleTimer])

  // ç‚¹å‡»å®¹å™¨å”¤é†’ï¼ˆä»…æ‰‹æœºç«¯ï¼‰
  const handleContainerClick = (e: React.MouseEvent) => {
    if (!isMobile) return // PCç«¯ä¸éœ€è¦å”¤é†’é€»è¾‘
    if (isIdle) {
      e.stopPropagation()
      resetIdleTimer(true) // æ ‡è®°ä¸ºå”¤é†’
    }
  }

  const toggleTheme = () => {
    if (hasMoved.current) return // æ‹–åŠ¨åä¸è§¦å‘ç‚¹å‡»
    if (isMobile && (isIdle || justWokeUp.current)) return // æ‰‹æœºç«¯å¿…é¡»å®Œå…¨å‡ºæ¥æ‰èƒ½ç‚¹å‡»
    const newTheme = theme === 'black' ? 'white' : 'black'
    setTheme(newTheme)
  }

  const scrollToTop = () => {
    if (hasMoved.current) return // æ‹–åŠ¨åä¸è§¦å‘ç‚¹å‡»
    if (isMobile && (isIdle || justWokeUp.current)) return // æ‰‹æœºç«¯å¿…é¡»å®Œå…¨å‡ºæ¥æ‰èƒ½ç‚¹å‡»

    // å°è¯• docs é¡µé¢çš„æ»šåŠ¨å®¹å™¨
    const docsContent = document.querySelector('[data-docs-content]')
    if (docsContent) {
      docsContent.scrollTo({ top: 0, behavior: 'smooth' })
      // åŒæ—¶æ»šåŠ¨å·¦ä¾§æ–‡æ¡£åˆ—è¡¨å’Œå³ä¾§ç›®å½•åˆ°é¡¶éƒ¨
      const docsSidebars = document.querySelectorAll('[data-docs-sidebar]')
      docsSidebars.forEach(sidebar => {
        sidebar.scrollTo({ top: 0, behavior: 'smooth' })
      })
    } else {
      window.scrollTo({ top: 0, behavior: 'smooth' })
    }
  }

  // é¼ æ ‡è¿›å…¥æ—¶æ¢å¤ï¼ˆä»…æ‰‹æœºç«¯ï¼‰
  const handleMouseEnter = () => {
    if (!isMobile) return // PCç«¯ä¸éœ€è¦å”¤é†’é€»è¾‘
    if (isIdle) {
      resetIdleTimer(true) // æ ‡è®°ä¸ºå”¤é†’
    }
  }

  // åå°é¡µé¢ä¸æ˜¾ç¤ºæ‚¬æµ®çƒ
  if (isAdmin || !mounted) {
    return null
  }

  const buttonClass = `
    w-12 h-12 rounded-full shadow-lg border-2 transition-all duration-300
    flex items-center justify-center relative overflow-hidden
    hover:scale-110 active:scale-95
    ${
      theme === 'black'
        ? 'bg-bg-secondary text-text-primary border-border-primary hover:bg-bg-tertiary'
        : 'bg-bg-tertiary text-text-primary border-border-primary hover:bg-bg-secondary'
    }
  `

  // è®¡ç®—ä½ç§»ï¼šæ‰‹æœºç«¯æ‹–åŠ¨ä½ç½® + idleæ—¶å‘å³éšè—ï¼ˆPCç«¯ä¸å‚ä¸ï¼‰
  const getTransform = () => {
    let x = isMobile ? position.x : 0
    let y = isMobile ? position.y : 0

    // idleçŠ¶æ€æ—¶å‘å³ç§»åŠ¨40pxï¼ˆåŠéšè—æ•ˆæœï¼‰- ä»…æ‰‹æœºç«¯
    if (isMobile && isIdle && !isDragging) {
      x += 40
    }

    return `translate(${x}px, ${y}px)`
  }

  // è®¡ç®—é€æ˜åº¦ï¼šPCç«¯æ°¸è¿œä¸º1ï¼ˆå®Œå…¨æ˜¾ç¤ºï¼‰
  const getOpacity = () => {
    if (!isMobile) return 1 // PCç«¯æ°¸è¿œå®Œå…¨æ˜¾ç¤º
    if (isDragging) return 0.8
    if (isIdle) return 0.3
    return 1
  }

  return (
    <div
      ref={containerRef}
      className="fixed right-4 md:right-8 z-[9998] flex flex-col gap-3 transition-all duration-500 ease-out cursor-pointer"
      style={{
        bottom: '102px',
        transform: getTransform(),
        touchAction: 'none',
        opacity: getOpacity()
      }}
      onClick={handleContainerClick}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
      onMouseEnter={handleMouseEnter}
    >
      {/* å›åˆ°é¡¶éƒ¨æŒ‰é’® */}
      <button
        onClick={scrollToTop}
        className={`${buttonClass} ${showScrollTop ? 'opacity-100' : 'opacity-0 pointer-events-none'}`}
        aria-label="å›åˆ°é¡¶éƒ¨"
      >
        <ChevronUp className="w-5 h-5" style={{ color: 'var(--text-primary)' }} />
      </button>

      {/* ä¸»é¢˜åˆ‡æ¢æŒ‰é’® */}
      <button
        onClick={toggleTheme}
        className={buttonClass}
        aria-label="åˆ‡æ¢ä¸»é¢˜"
      >
        {theme === 'black' ? (
          <Sun className="w-5 h-5" style={{ color: 'var(--text-primary)' }} />
        ) : (
          <Moon className="w-5 h-5" style={{ color: 'var(--text-primary)' }} />
        )}
      </button>
    </div>
  )
}
