'use client'

import { useEffect, useState } from 'react'
import { usePathname } from 'next/navigation'
import { useSettings } from '@/contexts/SettingsContext'
import { isAdminPage } from '@/lib/adminConfig'

// è®¾ç½® cookie
const setCookie = (name: string, value: string, days: number = 365) => {
  const expires = new Date()
  expires.setTime(expires.getTime() + days * 24 * 60 * 60 * 1000)
  document.cookie = `${name}=${value};expires=${expires.toUTCString()};path=/`
}

export default function GrayscaleOverlay() {
  const pathname = usePathname()
  const { settings, isLoading } = useSettings()
  const [mounted, setMounted] = useState(false)

  const isAdmin = isAdminPage(pathname)
  const themeColor = settings.themeColor

  useEffect(() => {
    setMounted(true)
  }, [])

  useEffect(() => {
    if (!mounted || isLoading) return

    // å°† themeColor å†™å…¥ cookie
    if (themeColor) {
      setCookie('themeColor', themeColor)
    }

    // å½“ä¸»é¢˜é¢œè‰²ä¸ºç°è‰²æ—¶ï¼Œè‡ªåŠ¨å°†ç”¨æˆ·ä¸»é¢˜è®¾ç½®ä¸ºé»‘è‰²
    if (themeColor === 'gray') {
      setCookie('theme', 'black')
      document.documentElement.setAttribute('data-theme', 'black')
    }

    // åå°é¡µé¢ä¸åº”ç”¨ç°åº¦æ•ˆæœ
    if (isAdmin) {
      document.documentElement.style.filter = ''
      document.body.style.filter = ''
      return
    }

    // å½“ä¸»é¢˜é¢œè‰²ä¸ºç°è‰²æ—¶ï¼Œåº”ç”¨å…¨å±€ç°åº¦æ•ˆæœåˆ° html å’Œ body
    if (themeColor === 'gray') {
      document.documentElement.style.filter = 'grayscale(100%)'
      document.body.style.filter = 'grayscale(100%)'
    } else {
      document.documentElement.style.filter = ''
      document.body.style.filter = ''
    }

    return () => {
      document.documentElement.style.filter = ''
      document.body.style.filter = ''
    }
  }, [themeColor, isAdmin, mounted, isLoading])

  // åå°é¡µé¢æˆ–éç°è‰²ä¸»é¢˜ä¸æ¸²æŸ“è’™ç‰ˆ
  if (!mounted || isAdmin || themeColor !== 'gray') {
    return null
  }

  // æ¸²æŸ“ä¸€ä¸ªæœ€é«˜å±‚çº§çš„ç°åº¦è’™ç‰ˆï¼Œç¡®ä¿è¦†ç›–æ‰€æœ‰å†…å®¹åŒ…æ‹¬åŠ è½½åŠ¨ç”»
  return (
    <div
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        zIndex: 99999,
        pointerEvents: 'none',
        backdropFilter: 'grayscale(100%)',
        WebkitBackdropFilter: 'grayscale(100%)',
      }}
    />
  )
}
